.landing-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}                  

.welcome h1 {
  font-weight: 400;
  margin: 0;
  font-size: 2.8em;
}

.welcome {
  /* grid-area: welcome; */
  margin: 10%;
  /* height: 70%; */
  position: relative;
}



.skills {
  /* grid-area: skills; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 8%;
  position: fixed;
  top: 50vh;
;
}

.skills section {

  border-radius: 50%;
  width: 90px;
  height: 90px;
  text-align: center;
  line-height: 90px;;
  margin: 3px;
  padding: 0;
  font-size: 18px;
}

.html{
  background-color: #ff9f80;
  grid-area: html;
  -webkit-animation: html .5s ease-in-out;
          animation: html .5s ease-in-out; 
}

@-webkit-keyframes html {
  0% {-webkit-transform: translateX(-100px);transform: translateX(-100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

@keyframes html {
  0% {-webkit-transform: translateX(-100px);transform: translateX(-100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

.css{
  grid-area: css;
  -webkit-animation: css 1s ease-in-out;
          animation: css 1s ease-in-out;
  background-color: #80bfff	;
}

@-webkit-keyframes css {
  0% {-webkit-transform: translateY(-500px);transform: translateY(-500px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

@keyframes css {
  0% {-webkit-transform: translateY(-500px);transform: translateY(-500px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

.ruby{
  background-color: #ff8080;
  grid-area: ruby;
  -webkit-animation: ruby .8s ease-in-out;
          animation: ruby .8s ease-in-out;
}

@-webkit-keyframes ruby {
  0% {-webkit-transform: translateX(100px);transform: translateX(100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

@keyframes ruby {
  0% {-webkit-transform: translateX(100px);transform: translateX(100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

.js{
  background-color: #ffdf80;
  grid-area: js;
  -webkit-animation: html .9s ease-in-out;
          animation: html .9s ease-in-out; 
}

.node{
  background-color: #80ff9f;
  grid-area: node;
  -webkit-animation: node 1s ease-in-out;
          animation: node 1s ease-in-out;
}

@-webkit-keyframes node {
  0% {-webkit-transform: translateY(300px);transform: translateY(300px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

@keyframes node {
  0% {-webkit-transform: translateY(300px);transform: translateY(300px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

.react{
  background-color: #80dfff;
  grid-area: react;
  -webkit-animation: ruby 1s ease-in-out;
          animation: ruby 1s ease-in-out;

}



@media only screen  and (min-width : 700px) {
  
  .welcome {
    margin: 5% 20%;
    height: 26vh;
    position: absolute;
  }

  
  .welcome h1 {
    font-weight: 400;
    margin: 0;
    font-size: 4.5em;
  }

  .skills {
    margin: 2% 20%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: fixed;
    top: 54%;
  }

  .skills section {
    width: 6em;
    height: 6em;
    text-align: center;
    line-height: 6em;;
    margin: 10px;
    padding: 0;
    font-size: 24px;
  }
  



}


