.header-grid {
  height: 2em;
  background-color: #bf80ff		;
  line-height: 2em;
  font-size: 2em;
  padding: 1vw 2vw;
  position: relative;
  color: whitesmoke;

}


.title {
  float: left;
  font-size: 1em;
  line-height: 1.5em;
  font-weight: 600;
  border: 3.5px solid whitesmoke;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
}

.nav {
  position: absolute;
  right: 5%;
  /* top: 10%; */
}


.links {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  display: none;
}

.dropdown {
  line-height: 2em;
}





