.card {
  width: 300px;
  height: 400px;
  /* border: 1px solid red; */
  background: #80bfff;
  margin: 0 auto;
  margin-bottom: 20px;
  position: relative;
  border-top: 3px solid #80bfff;
  border-radius: 2px 2px 0 0;
}

.card:hover{
  -webkit-box-shadow: 0px 1px 11px 0px rgba(102,89,102,0.64);
box-shadow: 0px 1px 11px 0px rgba(102,89,102,0.64);
}

.card-img {
  width: 100%;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.card-title {
  background: #ff9f80;
  color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 2em;
  padding: 3px;
}

.card-description {
  color: whitesmoke;
  padding: 3px;
  font-size: 1em;
}

.card-links {
  position: absolute;
  bottom: 1%;
}
.card-links a {
  -webkit-text-decoration: #ff9f80;
          text-decoration: #ff9f80;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: 	whitesmoke;
  font-weight: 700;
  padding: 3px;
  padding-right: 10px;
  font-size: 1.5em;
}

.card-links a:hover {
  -webkit-text-decoration: whitesmoke;
          text-decoration: whitesmoke;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: 	#ff9f80;
  font-weight: 700;
  padding: 3px;
  padding-right: 10px;
  font-size: 1.5em;
}

@media only screen  and (min-width : 700px) {
  
  .card {
    height: 500px;
  }
  
  .card-description{
    font-size: 20px;
    padding: 10px;
  }

}


