.drone-wrapper {
  text-align: center;
}

.drone-intro {
  padding: 3px;
  background: #80bfff;
  margin-bottom: 20px;
}

.drone-intro p {
  color: whitesmoke;
}

.insta-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.insta-card {
  margin: 0 auto;
  margin-bottom: 15px;
}


@media only screen  and (min-width : 700px) {
  .drone-wrapper {
    margin: 0 20%;
  }

  .drone-intro {
    padding: 10px;
    font-size: 1.5em;
  }
  
  .insta-wrapper {
    margin: 0 10%;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .insta-card {
    margin: 0 5px;
    margin-bottom: 15px;
  }
}


